import styled from "styled-components";

const ShortDivider = styled.div`
  margin: 25px 25% 20px;
  border-bottom: 1px solid #ddd;
  @media (min-width: 992px) {
    margin-top: 35px;
    margin-bottom: 30px
  }
`;

export default ShortDivider;



